<!-- after sales -->
<template>
  <div class>
    <el-dialog
      :title="$t('after sales')"
      :visible.sync="salesData.dialogVisible"
      width="30%"
      @open="open()"
      :before-close="cancel"
    >
      <div>
        <el-form ref="form" :model="form" label-width="140px">
          <el-form-item :label="$t('')" required>
            <el-select v-model="form.dealType" :placeholder="$t('Please select')">
              <el-option :label="$t('')" value="1"></el-option>
              <el-option :label="$t('')" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item :label="$t('')" required>
            <el-select v-model="form.problemType" :placeholder="$t('Please select')">
              <el-option
                :label="item.label"
                v-for="item in questionList"
                :key="item.id"
                :value="item.id"
              ></el-option>
              <!-- <el-option label="Replacement" value="2"></el-option> -->
            </el-select>
          </el-form-item>
          <el-form-item :label="$t('')">
            <el-input type="textarea" v-model="form.desc" :placeholder="$t('')"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel()">{{$t('')}}</el-button>
        <el-button type="primary" @click="save()">{{$t('')}}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
//jsjsjson
//import  from '';

export default {
  //import
  props: ['salesData'],
  components: {},
  data () {
    //
    return {
      form: {
        dealType: '',
        problemType: '',
        desc: '',
      },
      questionList: [
        { label: this.$t(''), id: 1 },
        { label: this.$t(''), id: 2 },
        { label: this.$t(''), id: 3 },
        { label: this.$t(''), id: 4 },
        { label: this.$t(''), id: 5 },
        { label: this.$t(''), id: 6 },
        { label: this.$t(''), id: 7 },
        { label: this.$t(''), id: 8 },
        { label: this.$t(''), id: 9 },
      ],

    }
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    handleClose () {

    },
    cancel () {
      this.form.dealType = ''
      this.form.problemType = ''
      this.form.desc = ''
      this.$emit('callBackSale')
    },
    open(){
      this.form.dealType = ''
      this.form.problemType = ''
      this.form.desc = ''
    },
    save () {
      let params = {
        accountId: this.salesData.shopifyAccountId,
        orderId: this.salesData.orderId,
        type: this.form.dealType,
        reasonType: this.form.problemType,
        description: this.form.desc
      }
      this.$emit('callBackSale')
      this.$apiCall("api.OrderRefund.afterService", params, {
      }, (r) => {
        console.log(r, '35235');
        if (r.ErrorCode == "9999") {
          //  this.$elementMessage(r.Message, "success");
          this.form.dealType = ''
          this.form.problemType = ''
          this.form.desc = ''
          this.$emit('callBackSale')
        } else {
          this.$elementMessage(r.Message, "error")
        }
      })
    }
  },
  // - this
  created () {

  },
  // - DOM
  mounted () {

  },
  beforeCreate () { }, // - 
  beforeMount () { }, // - 
  beforeUpdate () { }, // - 
  updated () { }, // - 
  beforeDestroy () { }, // - 
  destroyed () { }, // - 
  activated () { }, //keep-alive
}
</script>
